* {
    margin: 0;
    padding: 0;
}

.wrapper {
  display: grid;
    overflow: hidden;
  width: 1200px;
  margin: 0 auto;

  grid-template-areas:
    "h h"
    "n m";
  grid-template-rows: 60px 1fr;
  grid-template-columns: 2fr 10fr;

}

ul li {
  list-style: none;
}

a {
  text-decoration: none;
  color: inherit;
}

.main {
    grid-area: m;
    background: gainsboro;
}


